body {
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
}
#particles-js {
    width: 100vw;
    height: 100vh;
    background: whitesmoke;
    display: flex;
    align-content: center;
    justify-content: center;
}
.particles-js-canvas-el {
    position: absolute;
}
.main {
    position: absolute;
    top: 30%;
    left: 20%;
    width: 525px;
}
.header {
    position: absolute;
    z-index: 0;
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 100px;
    margin-top: 0;
    -webkit-user-select: none; /* Chrome/Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+ */
}
.content {
    position: absolute;
    z-index: 0;
    margin-left: 100px;
    padding-left: 25px;
    display: inline-block;
    background: rgb(245, 245, 245, 0.5);
    -webkit-user-select: none; /* Chrome/Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+ */
}
ul.header li {
    list-style-type: none;
}
ul.header li .nav{
    display: flex;
    border: 1px;
    border-radius: 10px;
    width: 100px;
    height: 60px;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(to bottom right, rgb(211, 211, 211, 0.5), rgb(245, 245, 245, 0.5));
    color: black;
    text-decoration: none;
}
ul.header .is-active {
    background: gray;
}
ul.header .nav:hover {
    background: grey;
    cursor: pointer;
}
.content h2 {
    margin-top: 0;
}
#icons {
    margin-top: 10px;
    margin-left: 0px;
}
#icons > li {
    list-style: none;
    padding: 10px;
    position: relative;
    
}
#icons > li > a > img {
    width: 80px;
}
ul > li > a > img:hover {
    transform: scale(1.1);
}

